import Peer from 'simple-peer';
export default {
  data: function data() {
    return {
      p: null,
      supported: Peer.WEBRTC_SUPPORT
    };
  },
  mounted: function mounted() {
    this.p = new Peer({
      initiator: true
    });
    this.p.on('error', this.onError);
    this.p.on('signal', this.onSignal);
    this.p.on('data', this.onData);
    this.p.on('connect', function () {
      console.log('SOMEONE CONNECT');
      this.p.send('whatever' + Math.random());
    });
  },
  methods: {
    onError: function onError(err) {
      console.log('error', err);
    },
    onSignal: function onSignal(data) {
      console.log('SIGNAL', JSON.stringify(data));
    },
    onData: function onData(data) {
      console.log('data: ' + data);
    },
    sendData: function sendData(data) {
      if (this.p.connected) this.p.send(data);else console.log('☢️ Not connected');
    }
  }
};